import { useEffect, useState } from "react";
import { useSession } from "next-auth/react";

type Patient = {
  id: number;
  firstName: string;
  lastName: string;
};

type InstructionOption = string;

export default function DoctorDashboard() {
  const { data: session, status } = useSession();
  const [patients, setPatients] = useState<Patient[]>([]);
  const [selectedPatientId, setSelectedPatientId] = useState<number | null>(null);
  const [diagnosis, setDiagnosis] = useState("");
  const [notes, setNotes] = useState("");
  const [medications, setMedications] = useState("");
  const [instruction, setInstruction] = useState("");
  const [file, setFile] = useState<File | null>(null);

  const instructionOptions: InstructionOption[] = [
    "Prendre 1 comprim√© toutes les 6 heures",
    "Appliquer localement 2 fois par jour",
    "Boire beaucoup d'eau",
    "Repos au lit pendant 48h",
    "Suivi en cabinet dans 7 jours",
  ];

  // Charger la liste des patients
  useEffect(() => {
    const fetchPatients = async () => {
      const res = await fetch("/api/patients", {
        headers: { "Content-Type": "application/json" },
      });
      if (res.ok) {
        const data = await res.json();
        setPatients(data);
      }
    };
    fetchPatients();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!selectedPatientId || !session?.user?.name) {
      alert("Veuillez s√©lectionner un patient et vous assurer d'√™tre connect√©.");
      return;
    }

    const patient = patients.find(p => p.id === selectedPatientId);
    if (!patient) return;

    // üëá Utiliser le nom r√©el du docteur depuis la session
    const doctorName = session.user.name;

    try {
      // 1. Cr√©er la consultation
      const consultRes = await fetch("/api/consultations", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          doctorName, // ‚úÖ Nom exact du docteur connect√©
          patientName: `${patient.firstName} ${patient.lastName}`.trim(),
          datetime: new Date().toISOString(),
          duration: 30,
          diagnosis,
          notes,
        }),
      });

      if (!consultRes.ok) {
        const err = await consultRes.json();
        throw new Error(err.error || "√âchec de la cr√©ation");
      }

      const createdConsult = await consultRes.json();

      // 2. Cr√©er l'ordonnance
      const prescRes = await fetch("/api/prescriptions", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          consultationId: createdConsult.id,
          medications: medications.split(",").map(m => m.trim()).filter(Boolean),
          instructions: instruction,
        }),
      });

      if (!prescRes.ok) throw new Error("√âchec de la cr√©ation de l'ordonnance");

      alert("Consultation cr√©√©e avec succ√®s !");

      // R√©initialiser
      setSelectedPatientId(null);
      setDiagnosis("");
      setNotes("");
      setMedications("");
      setInstruction(instructionOptions[0] || "");
      setFile(null);

    } catch (err: any) {
      console.error(err);
      alert(`Erreur : ${err.message}`);
    }
  };

  if (status === "loading") return <div>Chargement...</div>;
  if (!session) return <div>Non autoris√©</div>;

  return (
    <div style={{ padding: "20px" }}>
      <h2>Nouvelle Consultation</h2>
      <form onSubmit={handleSubmit}>
        {/* S√©lection patient */}
        <div style={{ marginBottom: "10px" }}>
          <label>Patient :</label>
          <select
            value={selectedPatientId || ""}
            onChange={(e) => setSelectedPatientId(Number(e.target.value))}
            required
          >
            <option value="">-- Choisir --</option>
            {patients.map((p) => (
              <option key={p.id} value={p.id}>
                {p.firstName} {p.lastName}
              </option>
            ))}
          </select>
        </div>

        {/* Diagnostic */}
        <div style={{ marginBottom: "10px" }}>
          <label>Diagnostic :</label>
          <textarea
            value={diagnosis}
            onChange={(e) => setDiagnosis(e.target.value)}
            required
          />
        </div>

        {/* Notes */}
        <div style={{ marginBottom: "10px" }}>
          <label>Notes :</label>
          <textarea
            value={notes}
            onChange={(e) => setNotes(e.target.value)}
          />
        </div>

        {/* M√©dicaments */}
        <div style={{ marginBottom: "10px" }}>
          <label>M√©dicaments (s√©par√©s par des virgules) :</label>
          <input
            type="text"
            value={medications}
            onChange={(e) => setMedications(e.target.value)}
            placeholder="Ex: Parac√©tamol, Ibuprof√®ne"
          />
        </div>

        {/* Instructions (liste) */}
        <div style={{ marginBottom: "10px" }}>
          <label>Instructions :</label>
          <select
            value={instruction}
            onChange={(e) => setInstruction(e.target.value)}
            required
          >
            {instructionOptions.map((opt, i) => (
              <option key={i} value={opt}>
                {opt}
              </option>
            ))}
          </select>
        </div>

        {/* Fichier (PDF/Image) ‚Äî fonctionnel mais non envoy√© √† l'API yet */}
        <div style={{ marginBottom: "10px" }}>
          <label>Scanner / IRM (PDF ou image) :</label>
          <input
            type="file"
            accept="image/*,.pdf"
            onChange={(e) => setFile(e.target.files?.[0] || null)}
          />
          {file && <p>Fichier s√©lectionn√© : {file.name}</p>}
        </div>

        <button type="submit">Cr√©er Consultation</button>
      </form>
    </div>
  );
}